import { Component } from '@angular/core';
import { NavController, Nav } from 'ionic-angular';
import { Http } from '@angular/http';
import { AlertController } from 'ionic-angular';
import { LoadingController } from 'ionic-angular';
import { HTTP } from '@ionic-native/http';
import { Storage } from '@ionic/storage';

import { Injectable } from '@angular/core';
import { Headers } from '@angular/http';
import 'rxjs/add/operator/map';


let apiUrl = 'http://68.66.207.110:8020/authenticate';

@Injectable()
export class AuthService {

  user = {};
  

  constructor(public navCtrl: NavController,private storage: Storage, public nav: Nav, private http: HTTP, private alertCtrl: AlertController,public loadingCtrl: LoadingController) {
    
  }
  

  login(credentials) {
    return new Promise((resolve, reject) => {
        var data1 = this.user;
        let headers = new Headers();
        headers.append('Content-Type', 'application/json');
        this.http.post('http://68.66.207.110:8020/authenticate', data1,{})
        .then(data => {
        let resp = JSON.parse(data["_body"]);
        var string = this.http.getCookieString('http://68.66.207.110:8020/authenticate');
        console.log(string);
        console.log(data.data);
        this.http.setCookie('http://68.66.207.110:8020/authenticate',string);
    
        this.http.post(apiUrl+'login', JSON.stringify(credentials), {headers: headers})
          .then(res => {
            resolve((res: Response) => res.json());
          }, (err) => {
            reject(err);
          });
        });
    });
    }

  register(data) {
    return new Promise((resolve, reject) => {
        let headers = new Headers();
        headers.append('Content-Type', 'application/json');
        var string = this.http.getCookieString('http://68.66.207.110:8020/authenticate');
        this.http.post(apiUrl+'signup', JSON.stringify(data), {headers: headers})
          .then(res => {
            resolve((res: Response) => res.json());
          }, (err) => {
            reject(err);
          });
    });
  }

  logout(){
    return new Promise((resolve, reject) => {
        let headers = new Headers();
        var string = this.http.getCookieString('http://68.66.207.110:8020/authenticate');
        headers.append('string', localStorage.getItem('cookie'));

        this.http.post(apiUrl+'logout', {}, {headers: headers})
          .then(res => {
            localStorage.clear();
          }, (err) => {
            reject(err);
          });
    });
  }
}
 



