import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { LoadingController } from 'ionic-angular';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';

@Component({
  selector: 'page-uploadimg',
  templateUrl: 'uploadimg.html'
})
export class UploadimgPage {

  myphoto:any; 

  constructor(private camera: Camera,private transfer: FileTransfer, private file: File,public loadingCtrl: LoadingController) {

  }

takephoto(){
  const options: CameraOptions = {
    quality: 70,
    destinationType: this.camera.DestinationType.FILE_URI,
    encodingType: this.camera.EncodingType.JPEG,
    mediaType: this.camera.MediaType.PICTURE
  }
  
  this.camera.getPicture(options).then((imageData) => {
   // imageData is either a base64 encoded string or a file URI
   // If it's base64 (DATA_URL):
   this.myphoto = 'data:image/jpeg;base64,' + imageData;
  }, (err) => {
   // Handle error
  });
}


getphoto(){
  const options: CameraOptions = {
    quality: 70,
    destinationType: this.camera.DestinationType.FILE_URI,
    sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
    saveToPhotoAlbum:false,
    allowEdit:true,
    targetWidth:400,
    targetHeight:400
  }
  
  this.camera.getPicture(options).then((imageData) => {
   // imageData is either a base64 encoded string or a file URI
   // If it's base64 (DATA_URL):
   this.myphoto = 'data:image/jpeg;base64,' + imageData;
  }, (err) => {
   // Handle error
  });
}

uploadfile(){
  let loader = this.loadingCtrl.create({
    content:"uploading..."

  });
  loader.present();

  const fileTransfer: FileTransferObject=this.transfer.create();
  
  var random=Math.floor(Math.random() *  100);

  let options: FileUploadOptions = {
    fileKey: 'photo',
    fileName: "myimage_" + random + ".jpg",
    chunkedMode:false,
    mimeType:"image/jpeg",
    headers: {}
   
 }

 fileTransfer.upload(this.myphoto,"http://192.168.1.30/api/uploadFoto.php", options)
 .then((data)=>{
   alert("success");
   loader.dismiss();
   this.presentToast(err);
 });
}

}


